ks-zaufane-product {
    display: block;
    box-sizing: border-box;
    max-width: 1200px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    background: white;
}

ks-zaufane-product .ekomi-widget-container .prc.container-fluid {
    padding: 20px;
}
ks-zaufane-product .ekomi-widget-container .row {
    margin: 0px !important;
}

ks-zaufane-product .prc .rating-details {
    background: none;
}

ks-zaufane-product .prc .review {
    border-bottom: 1px solid #e3e3e3;
}